body {
  width: 100%;
  height: 100vh;
  background-color: #000;
  overflow: hidden;
}

.img-morfiys {
  width: 650px;
  height: 600px;
  background-image: url("morfius.bg.08ad5fea.png");
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
}

.text-wrapper {
  color: bisque;
  justify-content: space-around;
  font-size: 20px;
  display: flex;
}

.butoon-wrapper {
  position: relative;
}

.js-start {
  position: absolute;
  bottom: 0;
  left: 30%;
}

.js-close {
  position: absolute;
  bottom: 0;
  right: 30%;
}

button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  padding: 80px;
}

.red {
  color: red;
}

.blue {
  color: #00f;
}

.back {
  position: absolute;
  top: 5px;
  left: 20px;
}

.back a {
  color: azure;
  text-decoration: none;
}

